<template>
    <div id="app">
        <router-view />

        <van-tabbar
            v-model="active"
            active-color="#e4393c"
            route
            v-if="active != '*'"
        >
            <van-tabbar-item
                name="shelf"
                replace
                to="/"
                icon="bar-chart-o"
            >
                书架
            </van-tabbar-item>
            <van-tabbar-item name="sort" replace to="/booksort" icon="points">
                分类
            </van-tabbar-item>
            <van-tabbar-item
                name="rank"
                replace
                to="/bookrank"
                icon="chart-trending-o"
            >
                排行
            </van-tabbar-item>
            <van-tabbar-item name="list" replace to="/booklist" icon="column">
                书单
            </van-tabbar-item>
        </van-tabbar>
        <LoadingView class="loading" :showLoading="loading"></LoadingView>
    </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import LoadingView from "@/components/LoadingView.vue";
import types from "@/store/types.js";
export default {
    name: "app",
    data() {
        return {
            active: "shelf",
        };
    },
    components: {
        LoadingView,
    },
    computed: {
        ...mapState({
            loading: (state) => state.loading,
        }),
    },
    methods: {
        ...mapMutations({ navchange: types.CHANGE_NAV_ACTIVE }),
    },
    watch: {
        active(val) {
            this.navchange({ active: val });
        },
    },
    beforeUpdate() {
        switch (this.$route.path) {
            case "/":
                this.active = "shelf";
                break;
            case "/booksort":
                this.active = "sort";
                break;
            case "/bookrank":
                this.active = "rank";
                break;
            case "/booklist":
                this.active = "list";
                break;
            default:
                this.active = "*";
                break;
        }
    },
};
</script>

<style lang="less">
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;

    .loading {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 9999;
        width: 100vw;
        height: 100vh;
        background-color: white;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
</style>
