import "../plugins/axios";

export const getData = function(url, params) {
    if (params) {
        return window.axios.get(url, { params: params });
    } else {
        return window.axios.get(url);
    }
};

export const TimeDifference = function(foramat) {
    let date = new Date(foramat);
    let updated = date.getTime();
    let now = Date.now();
    let d = now - updated;
    let update = "";
    if (Math.floor(d / 86400000 / 365) >= 1) {
        update = Math.floor(d / 86400000 / 365) + "年前";
    } else if (Math.floor(d / 86400000 / 30) >= 1) {
        update = Math.floor(d / 86400000 / 30) + "个月前";
    } else if (Math.floor(d / 86400000) >= 1) {
        update = Math.floor(d / 86400000) + "天前";
    } else if (Math.floor(d / 3600000) >= 1) {
        update = Math.floor(d / 3600000) + "小时前";
    } else if (Math.floor(d / 60000) >= 1) {
        update = Math.floor(d / 60000) + "分钟前";
    } else {
        update = "";
    }
    return update;
};

// json数据小说文章排版
export const AnalyzeArticle = function(str) {
    return str.split("\r\n").map((item) => {
        return item;
    });
};
