import Vue from "vue";
import Vuex from "vuex";
import types from "./types";
import { getData } from "../api/api";
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        navActive: "书架",
        loading: false,
        sortData: {},
        sortBookList: [],
        bookShortComments: [],
        bookComments: [],
        recommendList: [],
        chapterList: [],
        currentChapter: {},
        currentRankBooks: [],
    },
    mutations: {
        [types.CHANGE_NAV_ACTIVE](state, payload) {
            switch (payload.active) {
                case "shelf":
                    state.navActive = "书架";
                    break;
                case "sort":
                    state.navActive = "分类";
                    break;
                case "rank":
                    state.navActive = "排行";
                    break;
                case "list":
                    state.navActive = "书单";
                    break;
            }
        },
        [types.INIT_SORT_DATA](state, payload) {
            state.sortData = payload.data;
        },
        [types.GET_SORT_DETAIL_BOOKS](state, payload) {
            state.sortBookList = payload.data.books;
        },
        [types.GET_MORE_SORT_BOOKS](state, payload) {
            state.sortBookList = [...state.sortBookList, ...payload.data.books];
        },
        [types.CHANGE_LOADING_ACTIVE](state, payload) {
            state.loading = payload.loading;
        },
        [types.GET_BOOK_SHORT_COMMENT](state, payload) {
            state.bookShortComments = payload.data.docs;
        },
        [types.GET_BOOK_COMMENT](state, payload) {
            state.bookComments = payload.data.reviews;
        },
        [types.GET_RECOMMEND_BOOK](state, payload) {
            state.recommendList = payload.data.books;
        },
        [types.GET_BOOK_CHAPTER](state, payload) {
            state.chapterList = payload.data.chapters;
        },
        [types.GET_CURREN_BOOK_LINK](state, payload) {
            state.currentChapter = payload.data;
        },
        [types.GET_ONE_BOOK_LINK](state, payload) {
            state.chapterList = payload.chapters;
            state.currentChapter = payload.currentChapter;
        },
        [types.GET_RANK_BOOKS](state, payload) {
            state.currentRankBooks = payload.data.ranking.books;
        },
    },
    actions: {
        [types.INIT_SORT_DATA](context) {
            var sortData = JSON.parse(localStorage.getItem("sort"));

            if (sortData && sortData.expires > Date.now()) {
                context.commit(types.INIT_SORT_DATA, {
                    data: sortData.data,
                });
            } else {
                context.state.loading = true;
                getData("https://novel.kele8.cn/categories")
                    .then((res) => {
                        localStorage.setItem(
                            "sort",
                            JSON.stringify({
                                expires: Date.now() + 24 * 60 * 60 * 1000,
                                data: res.data,
                            })
                        );
                        context.commit(types.INIT_SORT_DATA, {
                            data: res.data,
                        });
                        context.state.loading = false;
                    })
                    .catch((err) => {
                        console.log(err);
                        context.state.loading = false;
                    });
            }
        },
        [types.GET_SORT_DETAIL_BOOKS](context, payload) {
            context.state.loading = true;
            getData(
                "https://api.kele8.cn/agent/https://novel.kele8.cn/category-info",
                payload
            )
                .then((res) => {
                    context.commit(types.GET_SORT_DETAIL_BOOKS, {
                        data: res.data,
                    });
                    context.state.loading = false;
                })
                .catch((err) => {
                    console.log(err);
                    context.state.loading = false;
                });
        },
        [types.GET_MORE_SORT_BOOKS](context, payload) {
            context.state.loading = true;
            getData(
                "https://api.kele8.cn/agent/https://novel.kele8.cn/category-info",
                payload
            )
                .then((res) => {
                    if (res.data.length != 0) {
                        context.commit(types.GET_MORE_SORT_BOOKS, {
                            data: res.data,
                        });
                    } else {
                        Vue.$toast({
                            message: "此类没有更多的书籍了",
                            forbidClick: true,
                            duration: 2000,
                            icon: "fail",
                        });
                    }
                    context.state.loading = false;
                })
                .catch((err) => {
                    console.log(err);
                    context.state.loading = false;
                });
        },
        [types.GET_BOOK_SHORT_COMMENT](context, payload) {
            let shortCommentStorage = JSON.parse(
                localStorage.getItem("sc-" + payload.book)
            );
            if (
                shortCommentStorage &&
                shortCommentStorage.expires > Date.now()
            ) {
                context.commit(types.GET_BOOK_SHORT_COMMENT, {
                    data: shortCommentStorage.data,
                });
            } else {
                context.state.loading = true;
                getData("https://novel.kele8.cn/book/short-reviews", payload)
                    .then((res) => {
                        context.commit(types.GET_BOOK_SHORT_COMMENT, {
                            data: res.data,
                        });
                        localStorage.setItem(
                            "sc-" + payload.book,
                            JSON.stringify({
                                expires: Date.now() + 24 * 60 * 60 * 1000,
                                data: res.data,
                            })
                        );
                        context.state.loading = false;
                    })
                    .catch((err) => {
                        console.log(err);
                        context.state.loading = false;
                    });
            }
        },
        [types.GET_BOOK_COMMENT](context, payload) {
            let bookCommentData = JSON.parse(
                localStorage.getItem("c-" + payload.book)
            );
            if (bookCommentData && bookCommentData.expires > Date.now()) {
                context.commit(types.GET_BOOK_COMMENT, {
                    data: bookCommentData.data,
                });
            } else {
                context.state.loading = true;
                getData("https://novel.kele8.cn/book/reviews", payload)
                    .then((res) => {
                        localStorage.setItem(
                            "c-" + payload.book,
                            JSON.stringify({
                                expires: Date.now() + 24 * 60 * 60 * 1000,
                                data: res.data,
                            })
                        );
                        context.commit(types.GET_BOOK_COMMENT, {
                            data: res.data,
                        });
                        context.state.loading = false;
                    })
                    .catch((err) => {
                        console.log(err);
                        context.state.loading = false;
                    });
            }
        },
        [types.GET_RECOMMEND_BOOK](context, payload) {
            let recommendData = JSON.parse(
                localStorage.getItem("rec-" + payload.id)
            );
            if (recommendData && recommendData.expires > Date.now()) {
                context.commit(types.GET_RECOMMEND_BOOK, {
                    data: recommendData.data,
                });
            } else {
                context.state.loading = true;
                getData("https://novel.kele8.cn/recommend/" + payload.id)
                    .then((res) => {
                        localStorage.setItem(
                            "rec-" + payload.id,
                            JSON.stringify({
                                expires: Date.now() + 24 * 60 * 60 * 1000,
                                data: res.data,
                            })
                        );
                        context.commit(types.GET_RECOMMEND_BOOK, {
                            data: res.data,
                        });
                        context.state.loading = false;
                    })
                    .catch((err) => {
                        console.log(err);
                        context.state.loading = false;
                    });
            }
        },
        [types.GET_BOOK_CHAPTER](context, payload) {
            context.state.loading = true;
            getData(
                "https://api.kele8.cn/agent/https://novel.kele8.cn/book-sources",
                payload
            )
                .then((res) => {
                    let id = res.data[0]._id;
                    getData("https://novel.kele8.cn/book-chapters/" + id)
                        .then((res) => {
                            context.commit(types.GET_BOOK_CHAPTER, {
                                data: res.data,
                            });
                            context.state.loading = false;
                        })
                        .catch((err) => {
                            console.log(err);
                            context.state.loading = false;
                        });
                })
                .catch((err) => {
                    console.log(err);
                    context.state.loading = false;
                });
        },
        [types.GET_CURREN_BOOK_LINK](context, payload) {
            context.state.loading = true;
            let encodeUrl = encodeURIComponent(payload.link);
            getData("https://novel.kele8.cn/chapters/" + encodeUrl)
                .then((res) => {
                    context.commit(types.GET_CURREN_BOOK_LINK, {
                        data: res.data.chapter,
                    });
                    context.state.loading = false;
                })
                .catch((err) => {
                    console.log(err);
                    context.state.loading = false;
                });
        },
        [types.GET_ONE_BOOK_LINK](context, payload) {
            context.state.loading = true;
            getData(
                "https://api.kele8.cn/agent/https://novel.kele8.cn/book-sources",
                { view: "summary", book: payload.id }
            )
                .then((res) => {
                    let id = res.data[0]._id;
                    getData("https://novel.kele8.cn/book-chapters/" + id)
                        .then((res) => {
                            let chapters = res.data.chapters;
                            let link = chapters[payload.index].link;
                            let encodeUrl = encodeURIComponent(link);
                            getData(
                                "https://novel.kele8.cn/chapters/" + encodeUrl
                            )
                                .then((res) => {
                                    context.commit(types.GET_ONE_BOOK_LINK, {
                                        chapters: chapters,
                                        currentChapter: res.data.chapter,
                                    });
                                    context.state.loading = false;
                                })
                                .catch((err) => {
                                    console.log(err);
                                    context.state.loading = false;
                                });
                        })
                        .catch((err) => {
                            console.log(err);
                            context.state.loading = false;
                        });
                })
                .catch((err) => {
                    console.log(err);
                    context.state.loading = false;
                });
        },
        [types.GET_RANK_BOOKS](context, payload) {
            let rankBooksData = JSON.parse(
                localStorage.getItem("rankbook-" + payload.id)
            );

            if (rankBooksData && rankBooksData.expires > Date.now()) {
                context.commit(types.GET_RANK_BOOKS, {
                    data: rankBooksData.data,
                });
            } else {
                context.state.loading = true;
                getData("https://novel.kele8.cn/rank/" + payload.id)
                    .then((res) => {
                        localStorage.setItem(
                            "rankbook-" + payload.id,
                            JSON.stringify({
                                expires: Date.now() + 24 * 60 * 60 * 1000,
                                data: res.data,
                            })
                        );
                        context.commit(types.GET_RANK_BOOKS, {
                            data: res.data,
                        });
                        context.state.loading = false;
                    })
                    .catch((err) => {
                        console.log(err);
                        context.state.loading = false;
                    });
            }
        },
    },
});
